import React from 'react';
import './Header.css';
import { Link } from 'react-router-dom'
import { FaLaptopCode } from 'react-icons/fa'

class Header extends React.Component {
  render() {
    return (
      <div className="navbarHeader d-flex align-items-center justify-content-center">
        <nav className="container navbar navbar-expand-sm ">
          <div className="space-between" style={{ width: '1100px' }}>
            <Link to="/home" className='navText' >
              <div className='space-between' style={{ width: '110px' }}>
                <FaLaptopCode className="laptopIcon" />
                <p className="mb-0" style={{ paddingLeft: '0px', marginLeft: '0px' }}>Andrew King </p>
              </div>
            </Link>
            <div className="space-between" style={{ width: '300px' }}>
              <Link to="/home" className='navText' > Home </Link>
              <Link to="/about" className='navText' > About </Link>
              <Link to="/designs" className='navText' > Work </Link>
              <a href={"https://docs.google.com/document/d/1UGsSHjQc2uxM-Psygvig9GexNt0zpHe_XlXYBagqj80/edit?usp=sharing"} target="_blank" rel="noopener noreferrer">
                <button className='mb-0 btn btn-primary'> Resume </button>
              </a>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default Header;
